@import './variables.scss';

* {
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
}

html,
body {
  font-family: $fontFam;
}

h1,
h2,
h3,
h4,
h5 {
  font-family: inherit;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.2rem;
  padding-bottom: 10px;
}

h3 {
font-size: 2.9rem;
padding-bottom: 10px;
}

h4 {
  font-size: 2.5rem;
  padding-bottom: 10px;
}

p {
  line-height: 1.5;
  font-size: 1.6rem;
  padding-bottom: 10px;
  font-family: $fontFam;
}

img {
  max-width: 100%;
  height: auto;
}



